.bg {
    position: fixed;
    top: 0;
    left: 0;
  
    /* preserved aspet ratio */
    min-width: 100%;
    min-height: 100%;
    filter: brightness(50%);
  }
  
  .show {
    position: absolute;
    top: 23%;
    left: 20%;
    z-index: 8;
  
    display: flex;
    align-items: center;
  }
  
  .img {
    height: 345px;
  }
  
  .textbox {
    padding: 0 16px 0 32px;
    width: 800px;
    height: 345px;
  }
  
  .title {
    color: white;
    font-weight: bold;
    font-size: 28px;
  }
  
  .title:hover {
    text-decoration: underline;
  }
  
  .title a {
    color: white;
    text-decoration: none;
  }
  
  .textbox ul {
    padding: 0 0 0 20px;
    color: white;
    font-size: 18px;
  }
  
  .textbox p {
    color: whitesmoke;
    font-weight: 300;
    font-size: 14px;
  }
  
  .desc {
    color: white;
  }
  
  @media screen and (max-width: 1090px) {
    .show {
      position: absolute;
      top: 18%;
      left: 10%;
      z-index: 8;
  
      display: flex;
      align-items: center;
    }
    .textbox {
      padding: 0 0px 0 32px;
      width: 400px;
    }
  }
  
  
  